const PostContent = ({
  url,
  title,
  type,
  text,
  position,
  consonant,
  vowel,
  commentCount,
  showFullPost,
  ...details
}) => (
  <React.Fragment>
    <TableCell>
      <Typography
        color="textSecondary"
        variant="h5">
        {title}
      </Typography>
    </TableCell>
    <Hidden xsDown>
      <TableCell align="center">
        <Typography
          color="textSecondary">
          {position}
        </Typography>
      </TableCell>
    </Hidden>
    <Hidden xsDown>
      <TableCell align="center">
        <Typography
          color="textSecondary">
          {availableCharacters.find(o => o.id === consonant).name}
        </Typography>
      </TableCell>
    </Hidden>
    <Hidden xsDown>
      <TableCell align="center">
        <Typography
          color="textSecondary">
          {availableCharacters.find(o => o.id === vowel).name}
        </Typography>
      </TableCell>
    </Hidden>
  </React.Fragment>
);

export default PostContent;
